
@charset "utf-8";
@import "//unpkg.com/mint-ui/lib/style.css";
html {
  font-family: Helvetica,STHeiti STXihei, Microsoft JhengHei, Microsoft YaHei, Arial;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
* {
  box-sizing: border-box;
}
body {
  margin: 0 auto;
  padding: 0px;
  font-size: 12px;
  font-family: "Microsoft YaHei" ! important;
  /*text-align:center; */
  min-width: 320px;
  max-width: 640px;
}
p,
ul,
ol,
li,
dl,
dt,
dd,
h1,
h2,
h3,
h4,
h5,
form,
fieldset,
legend,
input,
select,
textarea,
button,
th,
td,
menu,
article,
pre,
xmp,
plaintext,
listing {
  margin: 0;
  padding: 0;
}
pre,
xmp,
plaintext,
listing {
  white-space: normal;
}
article,
aside,
dialog,
figure,
footer,
header,
hgroup,
nav,
section,
select,
time {
  display: block;
}
h1,
h2,
h3,
h4,
h5,
h6,
input,
select,
button,
label {
  font-size: 100%;
  font-weight: 100;
  vertical-align: middle;
}
ul,
dl,
ol {
  list-style: none;
}
i,
em {
  font-style: normal;
}
b {
  font-weight: normal;
}
img,
fieldset,
input[type="submit"] {
  border: none;
}
input {
  outline: none;
  background: transparent;
  vertical-align: top;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
button {
  cursor: pointer;
  border: none;
}
textarea {
  word-wrap: break-word;
  resize: none;
}
/* 去除IE下拉菜单箭头 */
select::-ms-expand {
  display: none;
}
/* 去除IE下radio、checkbox默认样式 */
::-ms-check {
  display: none;
}
/* 去除输入框、文本域默认边框和IOS4下的内阴影 */
input,
textarea,
select {
  -webkit-appearance: none;
  border: none;
  outline: none;
}
/* 设置Gecko内核下输入框属性placeholder中值的文件颜色 */
input:-moz-placeholder {
  color: #C4C4C4;
}
/* 设置webkit内核下输入框属性placeholder中值的文件颜色 */
::-webkit-input-placeholder {
  color: #C4C4C4;
}
/* 设置webkit内核下文字大小不小于12px */
html,
body {
  -webkit-text-size-adjust: none;
  /*height:100%;*/
}
/* 禁止长按链接、按钮、输入框出现系统默认弹框菜单 */
a,
button,
input {
  -webkit-touch-callout: none;
  outline: none;
}
a {
  text-decoration: none;
  -webkit-touch-callout: none;
  color: #666;
}
* {
  -webkit-tap-highlight-color: rgba(255, 0, 0, 0);
  font-family: "Microsoft YaHei" ! important;
}
body {
  font-size: 0.4rem;
}
.icon {
  width: 1em;
  height: 1em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
}
#app {
  font-family: Helvetica, STHeiti STXihei, Microsoft JhengHei, Microsoft YaHei, Arial;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-align: center;
  color: #2c3e50;
}
.app {
  width: 100%;
  height: 100%;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}
.router-fade-enter-active,
.router-fade-leave-active {
  transition: opacity .3s;
}
.router-fade-enter,
.router-fade-leave-active {
  opacity: 0;
}
.mint-msgbox-btn.mint-msgbox-confirm {
  color: #FE403B!important;
}
img[lazy=loading] {
  width: 40% !important;
  display: block;
  height: 100%;
  margin: 0 auto;
}



